  font-weight: 500;
  color: var(--text-color);
}

.client-card-owner {
  font-size: 0.8125rem;
  color: var(--text-color-secondary);
}

.client-card-action {
  color: var(--text-color-secondary);
  opacity: 0;
  transition: opacity 0.2s ease, transform 0.2s ease;
}
.client-card:hover .client-card-action, .client-card:focus .client-card-action, .client-card.focused .client-card-action {
  opacity: 1;
  transform: translateX(2px);
